import styled from 'styled-components';

export const MainDiv = styled.div `
    grid-area: CT;
    position: relative;
    padding: 20px;
    background-color: #F5F6F8;
    -webkit-box-shadow: inset 0 0 6px rgb(0 0 0 / 50%);

    overflow-y: scroll;
    /* box-shadow: inset 0px 0px 11px; */
    
    ::-webkit-scrollbar{
        display:none;
    }
    
`;