import React from 'react';
import { Buttons } from '../../Bordero/Button/button';
import { Inputs } from '../Inputs/input.jsx';
import { useState } from "react";
import { Selects } from '../SelectDificuldade/select.jsx'
import { useEffect } from 'react';
import axios from 'axios';

function NewQuestion({handleSubmit, btnText, questionData}){

    const [dificuldade, setDificuldade] = useState([])
    const [question, setQuestion] = useState(questionData || {})

    useEffect(()=>{
        axios.get(`http://localhost:4000/api/Dificuldade`).then((response)=>{
                setDificuldade(response.data);
            })
    },[])

    const submit = (e) => {
        e.preventDefault();
        handleSubmit(question)
    }

    function handleChange(e){
        setQuestion({...question, [e.target.name]: e.target.value})
    }

    function handleDificulty(e){
        setQuestion({...question,
            dificuldade: {
                id: e.target.value,
                dificuldade: e.target.options[e.target.selectedIndex].text
            }
        }) 
    }
    return(
        <form onSubmit={submit} className="form-pergunta">
                <div className="row">
                        <div className="col-md-12">
                            <Inputs 
                            className="form-control"
                            type="text"
                            text="Pergunta" 
                            name="pergunta"
                            placeholder="Digite a pergunta"
                            handleOnChange={handleChange}
                            value={question.pergunta ? question.pergunta : ''}
                            />
                        </div>
                </div>
                <div className="row">
                    <div className="col-md-6">
                        <Inputs 
                        className="form-control"
                        type="text"
                        text="Alternativa 1" 
                        name="alternativa_um"
                        placeholder="Digite a Alternativa"
                        handleOnChange={handleChange}
                        value={question.alternativa_um ? question.alternativa_um : ''}
                        />
                    </div>
                    <div className="col-md-6">
                        <Inputs 
                        className="form-control"
                        type="text"
                        text="Alternativa 2" 
                        name="alternativa_dois"
                        placeholder="Digite a Alternativa"
                        handleOnChange={handleChange}
                        value={question.alternativa_dois ? question.alternativa_dois : ''}
                        />
                    </div>
                </div>
                <div className="row">
                    <div className="col-md-6">
                        <Inputs 
                        className="form-control"
                        type="text"
                        text="Alternativa 3" 
                        name="alternativa_tres"
                        placeholder="Digite a Alternativa"
                        handleOnChange={handleChange}
                        value={question.alternativa_tres ? question.alternativa_tres : ''}
                        />
                    </div>
                    <div className="col-md-6">
                        <Inputs 
                        className="form-control"
                        type="text"
                        text="Alternativa 4" 
                        name="alternativa_quatro"
                        placeholder="Digite a Alternativa"
                        handleOnChange={handleChange}
                        value={question.alternativa_quatro ? question.alternativa_quatro : ''}
                        />
                    </div>
                </div>
                <div className="row">
                    <div className="col-md-6">
                        <Inputs 
                        className="form-control"
                        type="text"
                        text="Resposta" 
                        name="resposta"
                        placeholder="Digite a Alternativa"
                        handleOnChange={handleChange}
                        value={question.resposta ? question.resposta : ''}
                        />
                    </div>
                </div>
                <div className="row">
                    <div className="col-md-12">
                        <Selects 
                        text="Selecione a Dificuldade"
                        name="id"
                        options={dificuldade}
                        handleOnChange={handleDificulty}
                        value={question.dificuldade ? question.dificuldade.id : ''}/>
                    </div>
                </div>
                <Buttons 
                type="submit"
                className="btn" 
                text={btnText}
                id="btnCadastrarPergunta"/>
            </form>
    )
}

export default NewQuestion;