import React, { useEffect } from "react";
import {LayoutGameDiv} from './style.js';
import { useState } from "react";
import axios from 'axios';
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome';
import { faForward } from '@fortawesome/free-solid-svg-icons'

export const LayoutGame = () =>{
    const [showScore, setShowScore] = useState(false);
    const [dificulties, setDificulties] = useState([])
    const [showDificulties, setShowDificulties] = useState(false);
    const [score, setScore] = useState(0);
    const [currentQuestion, setCurrentQuestion] = useState(0);
    const [startGame, setStartGame] = useState(false);
    const [questions, setQuestions] = useState([])
  
    async function getQ(id){
      await axios.get(`http://localhost:4000/api/PerguntaRandomFacil/${id}`).then((response)=>{
          setQuestions(response.data);
      })
      await setStartGame(true);
    }

    useEffect(()=>{
      axios.get(`http://localhost:4000/api/Dificuldade`).then((response)=>{
        setDificulties(response.data);
      })
  },[])

    function handleStart(id){
      getQ(id);
      
    }
  
    function handleRestart(){
      setStartGame(false)
      setShowScore(false)
      setScore(0)
      setCurrentQuestion(0)
    }

    function handleDificulties(){
      setStartGame(false)
      setShowDificulties(true)
      setShowScore(false)
      setScore(0)
      setCurrentQuestion(0)
    }
  
    function handleAnswer(isCorrect){
      if(isCorrect == questions[currentQuestion].resposta){
        setScore(score + 1);
      }
  
      const nextQuestion = currentQuestion + 1;
      if(nextQuestion < questions.length){
        setCurrentQuestion(nextQuestion);
      } else{
        setShowScore(true);
      }
    }
    return(
        <LayoutGameDiv>
      {!showDificulties ? (<div className='quizGame'>
      <div className='start-section'>
        <h2>Bem-vindo ao Quiz Game</h2>
        <div className='start-rules-section'>
          <ol>
            <li>Voc tem 30 segundos para cada pergunta.</li>
            <li>Durante o quiz, voc tem direito a 3 pulos e 3 ajudas</li>
            <li>Caso voc no responda dentro do tempo, o jogo se encerra</li>
            <li>No  permitido roubar</li>
          </ol>
        </div>
        <div className='start-button-section'>
          <button onClick={() => handleDificulties()}>Novo Jogo</button>
        </div>
      </div> </div>) : !startGame ? (<div className='quizGame'>
      <div className='dificultie-section'>
        <h2>Selecione a dificuldade</h2>
        <div className='start-dificultie-section'>
          <ul>
            {dificulties.map((key)=>(<li><div className='start-button-section'>
                <button onClick={() => handleStart(key.id)}>{key.dificuldade}</button>
            </div></li>))}
          </ul>
        </div>
      </div> </div>) : showScore ? (<div className='quizGame'>
        <div className='end-section'>
          <div className='score-section'>
            {score > 4 ? (<div className='score-msg-section'>
            <h2>Voc acertou {score} de {questions.length}</h2>
            <p>Incrvel, voc acertou todas!</p></div>)
            : score > 3 ? (<div className='score-msg-section'>
            <h2>Voc acertou {score} de {questions.length}</h2>
            <p>Voc est quase l!</p></div>)
            : score > 2 ? (<div className='score-msg-section'>
            <h2>Voc acertou {score} de {questions.length}</h2>
            <p>Nada mal, hein!</p></div>)
            : score > 1 ? (<div className='score-msg-section'>
            <h2>Voc acertou {score} de {questions.length}</h2>
            <p>No limite!</p></div>)
            : score > 0 ? (<div className='score-msg-section'>
            <h2>Voc acertou {score} de {questions.length}</h2>
            <p>No se desanime.</p></div>)
            : (<div className='score-msg-section'>
            <h2>Voc acertou {score} de {questions.length}</h2>
            <p>Precisa estudar mais.</p></div>)
            }
          </div>
          <div className='score-button-section'>
            <button onClick={() => handleRestart()}>Reiniciar</button>
          </div>
        </div>
        </div>) : (
        <div className='quizGame'>
          <div className='question-section'>
            <div className='question-count'>
              <span>Questo {currentQuestion + 1}</span>/{questions.length}
            </div>
            <div className='question-text'>
              {questions[currentQuestion].pergunta}
            </div>
            <div className='question-assets'>
              {/* <div className='question-assets-jump'>
                <ul>
                  <li><button><FontAwesomeIcon icon={faForward}/></button></li>
                  <li><button><FontAwesomeIcon icon={faForward}/></button></li>
                  <li><button><FontAwesomeIcon icon={faForward}/></button></li>
                </ul>
              </div>
              <div className='question-assets-discard'>
                <ul>
                  <li><button></button></li>
                  <li><button></button></li>
                  <li><button></button></li>
                </ul>
              </div> */}
            </div>
          </div>  

          <div className='answer-section'>
            <button onClick={() => handleAnswer(questions[currentQuestion].alternativa_um)}>{questions[currentQuestion].alternativa_um}</button>
            <button onClick={() => handleAnswer(questions[currentQuestion].alternativa_dois)}>{questions[currentQuestion].alternativa_dois}</button>
            <button onClick={() => handleAnswer(questions[currentQuestion].alternativa_tres)}>{questions[currentQuestion].alternativa_tres}</button>
            <button onClick={() => handleAnswer(questions[currentQuestion].alternativa_quatro)}>{questions[currentQuestion].alternativa_quatro}</button>
          </div>
        </div>
      )
    }
        </LayoutGameDiv>
    )
}